@charset "UTF-8";

.app-sidebar .mCSB_draggerContainer {
  left: -11px !important;
}

@media (min-width: 768px) {
  .app.sidenav-toggled {
    .app-content {
      margin-right: 0;
    }

    .app-sidebar {
      right: -250px !important;
    }

    .app-sidebar__overlay {
      visibility: hidden;
    }
