  margin-top: 5px !important;
}

.side-menu {
  margin-bottom: 0;
  padding: 0;
  list-style: none;

  .slide {
    .side-menu__item {
      padding: 12px 14px 12px 22px;
      border-radius: 100px 0 0 100px;
      margin: 3px 0px 3px 10px;
    }

    &.active {
      .side-menu__item {
        background-color: transparent;
      }

      .side-menu__label, .side-menu__icon {
        color: $white;
      }
    }
  }
}

.side-menu__item {  
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  padding: 0;
  font-size: 14px;
  font-weight: 500;
  -webkit-transition: border-right-color 0.3s ease, background-color 0.3s ease;
  -o-transition: border-right-color 0.3s ease, background-color 0.3s ease;
  transition: border-right-color 0.3s ease, background-color 0.3s ease;

  /* color:$white; */
  height: 40px;

  &.active, &:hover, &:focus { 
    text-decoration: none;
    color: $white;
    background: linear-gradient(45deg, #f54266, $primary);
  }

  &.active .angle, &:hover .angle, &:focus .angle {
    color: $white;
  }
}

.app-sidebar .side-menu__item:hover .side-menu__label {
  color: rgb(255, 255, 255);
}


.side-menu__item {
  &.active .side-menu__icon, &:hover .side-menu__icon, &:focus .side-menu__icon {
    color: $white;
  }
}

.slide-item {
  &.active, &:hover, &:focus {
    text-decoration: none;
    color: #b5c1d2;
  }
}

.header-icons {
  fill: $white;
}

.slide-item {
  &.active, &:hover, &:focus { 
    text-decoration: none;
    color: $primary !important;
  }
}

.slide-menu .sub-slide-menu {
  padding: 0 23px;
}

.sub-slide .sub-angle {
  margin-right: auto;
  float: right;
  font-size: 12px;
  margin-top: 3px;
}

.slide-menu li {
  position: relative;
}

.slide.is-expanded {
  a {
    color: #4c516d;
    text-decoration: none;
    font-size: 0.845rem !important;
    font-weight: 400;
  }

  .angle {
    color: $white;
  }

  .side-menu__item {
    background: linear-gradient(45deg, #f54266, $primary);

    .side-menu__label, .side-menu__icon {
      color: $white;
    }
  }

  .sub-side-menu__item:before {
    content: "\e932";
    font-family: 'feather' !important; 
    position: absolute;
    top: 9px;
    right: 21px;
    font-size: 13px;
    color: $black;
    opacity: 1;
  }
}

.side-menu .side-menu__icon {
  font-size: 18px;
  line-height: 0;
  margin-left: 14px;
  border-radius: 3px;
  text-align: center;
  color: #2d3144;
}

.side-menu__icon {
  -webkit-box-flex: 0;
  -ms-flex: 0 0 auto;
  flex: 0 0 auto;
  width: 25px;
}

.side-menu__label {
  white-space: nowrap;
  -webkit-box-flex: 1;
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
  align-items: center;
  white-space: nowrap;
  color: #2d3144;
  transition-property: color;
  -webkit-transition-property: color;
  position: relative;
  font-size: 0.875rem;
  line-height: 1;
  vertical-align: middle;
  font-weight: 500;
}

.slide.is-expanded .slide-menu {
  max-height: 100vh;
}

.slide-menu {
  max-height: 0;
  overflow: hidden;
  padding: 0;
  padding-right: 29px;
  list-style: none;
  padding: 0;
}

.slide-item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;

  /* padding: 6px 7px; */
  font-size: 14px;
  height: 32px;
  padding: 0 0 0 51px;
  font-weight: 200;

  .icon {
    margin-left: 5px;
  }
}

.angle {
  -webkit-transform-origin: center;
  -ms-transform-origin: center;
  transform-origin: center;
  -webkit-transition: -webkit-transform 0.3s ease;
  transition: -webkit-transform 0.3s ease;
  -o-transition: transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

